.weather {
  width: 350px;
  border-radius: 6px;
  box-shadow: 10px -2px 20px 2px rgb(0 0 0/40%);
  color: white;
  background-color: #333;
  margin: 50px auto 0 auto;
  padding: 0px 20px 20px 20px;
}

.top,
.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.city {
  font-weight: 600;
  font-size: 18px;
  line-height: 1;
  margin: 0;
  letter-spacing: 1px;
}

.weather-description {
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  margin-top: 4px;
  color: red;
}

.weather-icon {
  width: 100px;
}

.temperature {
  font-weight: 600px;
  font-size: 60px;
  width: auto;
  letter-spacing: -5px;
  margin: 10px 0;
}

.details {
  width: 50%;
  padding: 0 20px;
}

.parameter-row {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}
.parameter-label {
  text-align: left;
  font-weight: 400;
  font-size: 12px;
}

.parameter-value {
  font-weight: 600;
  text-align: right;
  font-size: 12px;
}

@media (max-width: 600px) {
  .weather {
    width: 80%;
  }
}
